.profile-page {
  justify-content: center;
  padding-top: 6rem;
  padding-left: 6rem;
  display: flex;
}

.profile-page__back {
  width: 6rem;
  height: 100vh;
  background-color: var(--color-light-bg);
  border-right: 1px solid var(--color-dark-bg);
  justify-content: center;
  align-items: center;
  padding: 1rem;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

