  width: 100%;
  margin-top: 6rem;
}

.profile-page__links {
  width: 100%;
  margin-top: 6rem;
  font-size: 1.4rem;
  font-weight: 600;
}

.profile-page__exit {
  color: var(--color-error);
}

.profile-page__btn-save {
  margin-top: 6rem;
}

